body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input {
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 14px;
}

#navbar {
  padding: 10px;
  width: 100vw;
  background-color: rgba(9, 91, 245, 0.5);
  font-size: 15px;
  position: fixed;
  display: flex;
  justify-content: space-between;
  color: white;
}

#content {
  display: flex;
  padding-top: 40px;
}

#form {
  display: flex;
  flex-direction: column;
  padding: 20px;
  height: calc(100vh - 40px);
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.form-input {
  display: flex;
  flex-direction: column;
}

.form-label {
  margin-bottom: 10px;
  margin-top: 10px;
}

.ui.secondary.menu {
  margin: 0px;
  padding: 0px;
}

#sms {
  padding: 20px;
  background: rgba(255, 255, 255, 1);
  margin: 30px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.sms {
  padding: 10px;
  margin: 10px;
  background: rgba(255, 255, 255, 1);
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex;
  justify-content: space-between;
  min-width: 60vw;
  height: 40px;
}

.sms div {
  /* margin-right: 200px; */
}

#options {
  display: flex;
}

#item-left {
  border-radius: 10px 0px 0px 10px;
}

#item-right {
  border-radius: 0px 10px 10px 0px;
}

.item {
  /* border-radius: 10px; */
  /* border-style: solid; */
  border-style: solid;
  padding: 10px;
  border-color: rgba(0, 0, 0, 0.3);
  color: rgba(0, 0, 0, 0.5);
}

.right {
  border-width: 1px 1px 1px 0px;
}

.left {
  border-width: 1px 0px 1px 1px;
}

.middle {
  border-width: 1px 1px 1px 1px;
}

.focused {
  background: rgba(5, 247, 5, 0.5);
  /* border-style: solid; */
  /* border-width: 1px; */
  border-color: rgba(0, 131, 0, 1);
  color: rgb(0, 131, 0);
}

button {
  float: right;
  margin-left: 10px;
  background-color: rgb(245, 181, 63);
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border-radius: 10px;
  width: 100px;
}

.button {
  padding: 7px 7px;
  display: inline-block;
  font-size: 12px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 10px;
  width: auto;
}

button:active {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 105, 180, 0.5),
    0 0 0 1.5px rgba(255, 105, 180, 0.5);
}

button:focus {
  outline: none;
  border: none;
}

#login-form {
  padding: 100px;
  display: flex;
}
